						<el-table-column fixed="right" label="操作" width="150" v-if="(is_add_page) || (is_editable == 1)">
							<template slot-scope="scope">
								<div class="" v-if="!is_add_page">
									<el-button v-if="scope.row.id != current_detail_id" @click="editDetail(scope.$index,scope.row)" type="text" size="small">编辑</el-button>
									<el-button v-if="scope.row.id == current_detail_id" @click="editSaveDetail(scope.$index,scope.row)" type="text" size="small">保存</el-button>
