								</div>
								<div class="" v-else>
									<div class="" v-if="scope.$index">
										<el-button v-if="addEditIndex!=scope.$index" @click="editDetailItem(scope.$index,scope.row)" type="text" size="small">编辑</el-button>
										<el-button v-if="addEditIndex!=scope.$index" @click="deleteDetailItem(scope.$index,scope.row)" type="text" size="small">删除</el-button>
