<script>
	import { getBackAddInfoMotherCashApi,getDetailProjectListMotherCashApi,addbackInfoMotherCashApi,getInitDataMotherCashBackApi,editSaveTotalMotherCashBackApi,editSaveDetailMotherCashBackApi,getEventByDateApi } from '../../config/api'
	import { dialogConfirm, dialogConfirmHasbtn,getSelectedItem } from '../../config/mUtils'
	export default {
		inject: ['reload'],
		
		data() {
			return {
				id: '',
				type: '',
				is_add_page: '',//是否是添加页面
				is_editable: '',//是否可编辑
				is_project: 0,
				edit_detail_fund_id: '',
				
				target_list: [],
				back_type_list: [],
				target_type_list: [ ],
				all_target_type_list: [ ],
				// 现金流总数据和详情数据开始
				addData: {
					id: 0,
					fund_id: '',
				},
				// 详情部分要添加的数据开始
				add_fund_id: '',
				add_fund_name: '',
				add_fund_type: '',
				add_back: '',
				add_profile: '',
				add_back_type: '',
				add_back_type_name: '',
				//编辑添加
				edit_fund_id: '',
				edit_fund_name: '',
				edit_fund_type: '',
				edit_back: '',
				edit_profile: '',
				edit_back_type: '',
				edit_back_type_name: '',
				// 详情部分要添加的数据结束
				datalistDetail: [ ],
				datalistTotal: [
					{
						unit: '',
						event: "",
						fund_id: '',
						fund_type: '',
						receive_date: '',
						fof_id: '',
						payback_type: '',
						back: '',
						profile: '',
						back_total: '',
						event_count: '',
					}
				],
				direct_project_list: [],
				
				count: '',//查看页面传过来的参数
				current_detail_id: -1,
				currentIndex: -1,

				addEditIndex: -1,
			}
		},
		created() {
			this.id = this.$route.query.id;
			this.type = this.$route.query.type;
			this.is_editable = this.$route.query.is_delete;
			this.datalistTotal[0].fof_id = this.$route.query.id;
			if (this.$route.query.is_add_page) {
				this.is_add_page = this.$route.query.is_add_page;
				this.makeDatalistDetail()
				this.getAddInfo()
			} else {
				this.count = this.$route.query.count;
				this.getAddInfo()
				this.getInitData()
			}
		},
		computed: {
			
		},
		mounted() { 
//			this.calculate()
		},
		methods: {
			// 添加功能模块开始
			getAddInfo(){
				getBackAddInfoMotherCashApi({
					fof_id: this.id,
					difference_sign: this.type
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.back_type_list = res.data.payload.back_type;
						this.target_type_list = res.data.payload.fund_types;
						this.all_target_type_list = res.data.payload.list;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			chooseTargetType(e){
				// //console.log(e);
				this.makeTargetList(e);
				if (e == 3) {
					this.is_project = 1;
				} else {
					this.is_project = 0;
				}
			},
			makeTargetList(type){
				var arr = [];
				var data = this.all_target_type_list;
				for (var i = 0,max = data.length;i<max;i++) {
					if (data[i].type == type) {
						arr.push(data[i]);
					}
				}
				this.target_list = arr;
			},
			chooseTarget(e){
				if (e) {
					this.getDetailProjectList()
					var item = getSelectedItem(this.target_list,'fund_id',e);
					if (!item) {
						return
					}
					this.datalistTotal[0].unit = item.unit;
					this.getEventCountData()
				} else {
					this.datalistTotal[0].unit = '';
					this.datalistTotal[0].event = '';
					this.datalistTotal[0].event_count = '';
				}
			},
			chooseDate(date){
				if (!date) {
					this.datalistTotal[0].event = '';
					this.datalistTotal[0].event_count = '';
					return
				}
				this.getEventCountData()
			},
			getEventCountData(){
				if (this.type == 1) {
					var type = 2;
				} else {
					var type = 3;
				}
				if (this.datalistTotal[0].fund_type == 1) {
					var fund_type = 2;
				} else if (this.datalistTotal[0].fund_type == 2){
					var fund_type = 4;
				} else if (this.datalistTotal[0].fund_type == 3){
					var fund_type = 5;
				} else if (this.datalistTotal[0].fund_type == 4){
					var fund_type = 3;
				} else {
					var fund_type = '';
				}
				if (this.is_add_page) {
					var event_id = '';
				} else{
					var event_id = this.datalistTotal[0].id;
				}
				var date = this.datalistTotal[0].receive_date;
				var fund_id = this.datalistTotal[0].fund_id;
				getEventByDateApi({
					type: type,
					id: this.id,
					cash_type: 3,
					date: date,
					fund_type: fund_type,
					fund_id: fund_id,
					event_id: event_id
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.datalistTotal[0].event = res.data.payload.event_name;
						this.datalistTotal[0]['event_count'] = res.data.payload.event_count;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			getDetailProjectList(){
				getDetailProjectListMotherCashApi({
					fund_type: this.datalistTotal[0].fund_type,
					fund_id: this.datalistTotal[0].fund_id,
					difference_sign: this.type
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.direct_project_list = res.data.payload;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			addDetailItem(e){
				var item = {};
				if (this.add_fund_id  && this.add_back && this.add_profile && this.add_back_type) {
					item = {
						fund_id: this.add_fund_id,
						fund_name: this.add_fund_name,
						fund_type: this.add_fund_type,
						back: this.add_back,
						profile: this.add_profile,
						back_type: this.add_back_type,
						back_type_name: this.add_back_type_name,
					}
					let filterObj = this.datalistDetail.filter(elment => {
						return this.add_fund_id == elment.fund_id
					});
					if(filterObj.length>0){
						dialogConfirm("info", '该回款项目已添加!');
					}else{
						dialogConfirm("success", '数据添加成功');
						this.datalistDetail.push(item);
						this.clearData()
						this.calculate()
					}
				} else{
					dialogConfirm("info", '数据添加失败');
				}
			},
			// 计算所有数据的总和
			calculate(){
				var data = this.datalistDetail;
				var back = 0;//人民币
				var profile = 0;
//				var totalShouldPay2 = 0;//美元
//				var totalHavePay2 = 0;
				for (var i=0,max=data.length;i<max;i++) {
					if(!data[i].back){
						data[i].should_pay = 0;
					}
					if(!data[i].profile){
						data[i].pay = 0;
					}
					back = Number(back) + Number(data[i].back);
					profile = Number(profile) + Number(data[i].profile);
				}
				this.datalistTotal[0].back = back;
				this.datalistTotal[0].profile = profile;
				this.datalistTotal[0].back_total = Number(this.datalistTotal[0].back) + Number(this.datalistTotal[0].profile);
			},
			//明细表下拉选择函数开始
			chooseDetailBackProject(e){
				//console.log(e);
				var item = this.getChoosedBackProject(e);
				this.add_fund_type = item.fund_type;
				this.edit_fund_type = item.fund_type;
				this.add_fund_name = item.name;
				this.edit_fund_name = item.name;
			},
			chooseDetailBackType(e){
				var item = this.getChoosedBackType(e);
				this.add_back_type_name = item.name;
				this.edit_back_type_name = item.name;
			},
			getChoosedBackProject(id){
				var data = this.direct_project_list;
				for (var i = 0,max = data.length;i<max;i++) {
					if (data[i].fund_id == id) {
						return data[i]
					}
				}
			},
			getChoosedBackType(e){
				var data = this.back_type_list;
				for (var i = 0,max = data.length;i<max;i++) {
					if (data[i].type == e) {
						return data[i]
					}
				}
			},
			//明细表下拉选择函数结束
			
			makeDatalistDetail(){
				this.datalistDetail.unshift(this.addData);
			},
			clearData(){
				this.add_fund_id = '';
				this.add_fund_name = '';
				this.add_fund_type = '';
				this.add_back = '';
				this.add_profile = '';
				this.add_back_type = '';
				this.add_back_type_name = '';
			},
			saveAddInfo(){
				var event = JSON.stringify(this.datalistTotal);
				if (this.datalistTotal[0].fund_type == 3) {
					var project_detail = [
						{
							fund_id: 0,
							fund_type: 0,
							back: this.datalistTotal[0].sum_back,
							profile: this.datalistTotal[0].sum_profile,
							back_type: this.datalistTotal[0].payback_type
						}
					];
					var detail = JSON.stringify(project_detail);
				} else{
					var arr = this.datalistDetail.slice(1);
					var detail = JSON.stringify(arr);
				}
				addbackInfoMotherCashApi({
					event: event,
					detail: detail,
					difference_sign: this.type
				}).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm("success", res.data.message);
						if (this.type == 1) {
							var name = 'motherFunRunDataCashFlowHome';
						} else{
							var name = 'selfAdministerFunRunDataCashFlowHome';
						}
						this.$router.push({
							name: name,
							query: {
								id: this.id
							}
						})
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			// 获取选中下拉所选的值 
			getSelectedItem(data,selectedId){
				let obj = {};
				obj = data.find((item)=>{
					return item.fund_id == selectedId;
				});
				return obj
			},
			editDetailItem(index,e){
				this.addEditIndex = index;
				this.edit_fund_id = e.fund_id;
				this.edit_fund_type = e.fund_type;
				this.edit_fund_name = e.fund_name;
				this.edit_back = e.back;
				this.edit_profile = e.profile;
				this.edit_back_type = e.back_type;
				this.edit_back_type_name = e.back_type_name;

				
			},
			cancleDetailItem(index,e){
				this.addEditIndex = -1;
			},
			saveDetailItem(index,e){
				let newArr = JSON.parse(JSON.stringify(this.datalistDetail));
				newArr.splice(index,1);
				let filterObj = newArr.filter(elment => {
					return this.edit_fund_id == elment.fund_id
				});
				if(filterObj.length>0){
					dialogConfirm('info','该回款项目已存在!');
				}else{
					this.datalistDetail[index].fund_id = this.edit_fund_id;
					this.datalistDetail[index].back = this.edit_back;
					this.datalistDetail[index].back_type = this.edit_back_type;
					this.datalistDetail[index].back_type_name = this.edit_back_type_name;
					this.datalistDetail[index].fund_name = this.edit_fund_name;
					this.datalistDetail[index].fund_type = this.edit_fund_type;
					this.datalistDetail[index].profile = this.edit_profile;
					this.addEditIndex = -1;
				}
			},
			deleteDetailItem(index,e){
				this.datalistDetail.splice(index,1);
			},
			addCancel(){
				this.$router.push({
					name: "cashFlowHome",
					query: {
						id: this.id
					}
				})
			},
			// 添加功能模块结束
			
			// 查看功能模块开始
			getInitData(){
				getInitDataMotherCashBackApi({
					count: this.count,
					difference_sign: this.type
				}).then(res => {
					//console.log(res);
					if(res.data.msgNo == 1) {
						this.datalistTotal = res.data.payload.event;
						this.datalistDetail = res.data.payload.detail;
						this.chooseTargetType(this.datalistTotal[0].fund_type);
						this.chooseTarget(this.datalistTotal[0].fund_id);
						if (this.datalistTotal[0].fund_type == 3) {
							this.is_project = 1;
						}
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			editTotal(index,row){
				//console.log(row);
				this.currentIndex = index;
			},
			editCancelTotal(index,row){
				this.currentIndex = -1;
				this.getInitData()
			},
			editSaveTotal(index,row){
				//console.log(row);
				editSaveTotalMotherCashBackApi({
					id: row.id,
					event: row.event,
					fund_id: row.fund_id,
					fund_type: row.fund_type,
					receive_date: row.receive_date,
					fof_id: this.id,
					payback_type: row.payback_type,
					difference_sign: this.type,
					event_count: row.event_count
				}).then(res => {
					//console.log(res);
					if(res.data.msgNo == 1) {
						dialogConfirm("success", res.data.message);
						this.reload()
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			editDetail(index,row){
				// console.log(this.datalistDetail);
				this.getInitData()
				this.current_detail_id = row.id;
			},
			editCancelDetail(index,row){
				this.current_detail_id = -1;
				this.getInitData()
			},
			editSaveDetail(index,row){
				// console.log(row);
				// console.log(this.datalistDetail);
				let filterObj = this.datalistDetail.filter(elment => {
					return row.fund_id == elment.fund_id
				});
				if(filterObj.length>1){
					dialogConfirm('info','该回款项目已存在!');
					return false;
				}
				editSaveDetailMotherCashBackApi({
					id: row.id,
					event_id: row.event_id,
					fund_id: row.fund_id,
					fund_type: row.fund_type,
					back: row.back,
					profile: row.profile,
					back_type: row.back_type,
					difference_sign: this.type
				}).then(res => {
					//console.log(res);
					if(res.data.msgNo == 1) {
						dialogConfirm("success", res.data.message);
						this.reload()
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			// 查看功能模块结束

		}
	}
</script>

<style lang="scss" scoped>
	.body {
		padding: 0 0px 30px 0px;
	}
	
	.company_name {
		height: 72px;
		border-bottom: 1px solid #EAEAEA;
	}
	
	.company_name_left {
		font-size: 18px;
		color: #33302D;
	}
	
	.company_name_left i {
		margin-right: 3px;
		color: #6292F4;
	}
	
	.company_name_right .delete {
		margin-right: 60px;
		color: #6292F4;
		font-size: 14px;
	}
	
	.cash_flow_table_title {
		font-size: 14px;
		color: #6292F4;
		margin: 22px 0px;
		.ts {
			color: #FF3B30;
			margin-left: 23px;
		}
		.name {
			color: #6292F4;
		}
	}
	
	.circle_blue {
		width: 7px;
		height: 7px;
		border-radius: 50%;
		margin-right: 5px;
		margin-left: 8px;
		background-color: #6292F4;
	}
	.submit_btn_box{
		margin: 35px 0 20px 0;
	}
	.submit_btn_box button{
		height: 36px;
		margin: 0 10px;
		line-height: 36px;
		padding: 0 25px;
		color: #6292F4;
		font-size: 14px;
		border-radius: 20px;
		border: 1px solid #6292F4;
	}
</style>


